<template>
  <div class="content" id="s-map">
    <!-- 设置容器ID，宽度1920PX，高度1080PX -->    
    <div id="myEchart" style="width:1320px; height:800px;"></div>
  </div>
</template>
 
<script>
  import axios from 'axios'
 // import Vue from 'vue'
 // Vue.prototype.$http=axios
//import $ from "jquery";/* 引入JQUERY，此处不赘述，用于调用$.get函数。 */
var echarts = require("echarts");/* 引入echarts模块，此处不赘述 */
 export default {
  data() {
    return {
      myChart: undefined,/* 定义变量 */
   //   ddd: null
    };
  },
  mounted() {
  var smap = document.getElementById('s-map');　
  var  chartMap = document.getElementById('myEchart');　　
  var resizeWorldMapContainer = function () {
               chartMap.style.width  = window.innerWidth +'px';
               chartMap.style.height = window.innerHeight-600 + 'px';
           };
           var myChart = echarts.init(chartMap);  /* 初始画布 */
           var ddd;
  axios.get('../static/world.json')
  .then(resp123 =>{ ddd = resp123.data;
   // console.log("asdfsdf-----for testing");
  //  console.log("data is ----1" + resp);
 //   console.log("data is ----2" + resp.data);
  //  console.log("data is -----3" + ddd);
   echarts.registerMap('world1',ddd);/* 注册world地图 */
 myChart.setOption({/* 设置myChart配置项 */
  tooltip: {
  trigger: 'item',
  formatter: '{b}'
    },
   series: [
   {
   type: 'map',
   mapType: 'world1', // 自定义扩展图表类型
   itemStyle: {
   // normal: { label: { show: true } }, // 普通状态下，显示标签
    emphasis: { label: { show: true } } // 高亮状态下，显示标签（默认）
    },
    roam: true, // 放大缩小、平移
     }
             ]})
                                 })
                                 .catch(function(error) { // 请求失败处理
 });
                                window.addEventListener("resize", function(){
  resizeWorldMapContainer();
  console.log("window changing");
  myChart.resize();      
       
          });
        }}
</script>


